 .TopPanel_menuToggle__2RBjl
 {
   display: block;
   position: fixed;
   top: 30px;
   left: 30px;
   
   z-index: 1;
   
   -webkit-user-select: none;
   -moz-user-select: none;
    -ms-user-select: none;
        user-select: none;
 }
 
 .TopPanel_menuToggle__2RBjl a
 {
   text-decoration: none;
   color: #232323;
   
   -webkit-transition: color 0.3s ease;
   
   transition: color 0.3s ease;
 }
 
 .TopPanel_menuToggle__2RBjl a:hover
 {
   color: tomato;
 }
 
 
 .TopPanel_menuToggle__2RBjl input
 {
   display: block;
   width: 40px;
   height: 32px;
   position: absolute;
   top: -7px;
   left: -5px;
   
   cursor: pointer;
   
   opacity: 0; /* hide this */
   z-index: 2; /* and place it over the hamburger */
   
   -webkit-touch-callout: none;
 }
 
 /*
  * Just a quick hamburger
  */
 .TopPanel_menuToggle__2RBjl span
 {
   display: block;
   width: 33px;
   height: 4px;
   margin-bottom: 5px;
   position: relative;
   
   background: #cdcdcd;
   border-radius: 3px;
   
   z-index: 1;
   
   -webkit-transform-origin: 4px 0px;
   
           transform-origin: 4px 0px;
   
   -webkit-transition: background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
               opacity 0.55s ease,
               -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
   
   transition: background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
               opacity 0.55s ease,
               -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
   
   transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
               background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
               opacity 0.55s ease;
   
   transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
               background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
               opacity 0.55s ease,
               -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
 }
 
 .TopPanel_menuToggle__2RBjl span:first-child
 {
   -webkit-transform-origin: 0% 0%;
           transform-origin: 0% 0%;
 }
 
 .TopPanel_menuToggle__2RBjl span:nth-last-child(2)
 {
   -webkit-transform-origin: 0% 100%;
           transform-origin: 0% 100%;
 }
 
 /* 
  * Transform all the slices of hamburger
  * into a crossmark.
  */
 .TopPanel_menuToggle__2RBjl input:checked ~ span
 {
   opacity: 1;
   -webkit-transform: rotate(45deg) translate(-2px, -1px);
           transform: rotate(45deg) translate(-2px, -1px);
   background: #232323;
 }
 
 /*
  * But let's hide the middle one.
  */
 .TopPanel_menuToggle__2RBjl input:checked ~ span:nth-last-child(3)
 {
   opacity: 0;
   -webkit-transform: rotate(0deg) scale(0.2, 0.2);
           transform: rotate(0deg) scale(0.2, 0.2);
 }
 
 /*
  * Ohyeah and the last one should go the other direction
  */
 .TopPanel_menuToggle__2RBjl input:checked ~ span:nth-last-child(2)
 {
   -webkit-transform: rotate(-45deg) translate(0, -1px);
           transform: rotate(-45deg) translate(0, -1px);
 }
 
 /*
  * Make this absolute positioned
  * at the top left of the screen
  */
 .TopPanel_menu__E6Aj3
 {
   position: absolute;
   width: 300px;
   margin: -100px 0 0 -50px;
   padding: 50px;
   padding-top: 125px;
   height: 800px;
   background: #ededed;
   list-style-type: none;
   -webkit-font-smoothing: antialiased;
   /* to stop flickering of text in safari */
   
   -webkit-transform-origin: 0% 0%;
   
           transform-origin: 0% 0%;
   -webkit-transform: translate(-100%, 0);
           transform: translate(-100%, 0);
   
   -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
   
   transition: -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
   
   transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
   
   transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0), -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
 }
 
 .TopPanel_menu__E6Aj3 li
 {
   padding: 10px 0;
   font-size: 22px;
 }
 
 /*
  * And let's slide it in from the left
  */
 .TopPanel_menuToggle__2RBjl input:checked ~ ul
 {
   -webkit-transform: none;
           transform: none;
 }
